import React from 'react'
import { motion } from 'framer-motion'
import { Link } from 'react-router-dom'
import img1 from '../assets/a1.jpg'
import img2 from '../assets/a2.jpg'
import img3 from '../assets/a3.jpg'

let txt = 'وقتی تصمیم به ایجاد یک ساختمان گرفته می شود ، ضرورت دارد فارغ از نوع کاربری آن ، درباره موارد کلی مراحل اصلی انجام یک پروژه ساختمانی مانند:نوع اسکلت ساختمان (بتنی یا فلزی)مراحل انجام کار از شروع تا پایان و زمان بندی و بودجه ،امکانات و موانع موجود بررسی و تصمیم گیری های لازم ، انجام شود.کلیه‌ مراحل ساخت در ساختمان های بتنی و یا فلزی با یکدیگر یکسان هستند. تنها تفاوت دو نوع ساختمان در مرحله‌ی اسکلت‌ بندی آن‌ها می باشد. در روش اسکلت فلزی ابتدا ستون‌ها، پل‌ها و تیرها ساخته می‌شوند و سپس پلکان بین طبقات قرار داده می‌شود.یعنی ستون‌ها قبل از قسمت‌های دیگر ساخته می‌شوند.همچنین ستون‌ها باید به دقت به یکدیگر جوش و یا پیچ شوند. سپس تمام کنترل های لازم انجام شود تا در اجرای مراحل بعد با مشکل روبرو نشوند.اما ساختمانی که تیر و ستون های آن از بتن و آرماتور تشکیل شده باشد ساختمان بتنی نامیده می شود .لازم به ذکر است که مقاومت ساختمان های فلزی در برابر آتش سوزی و حرارت نسبت به ساختمان بتنی کمتر می باشد.با تمام این تفاسیر شرایط مختلفی باعث می شود پروژه های ساختمانی مختلف از لحاظ: روش اجرا ،متریال'

const list = [
    {id:1, name:'نام پروژه',img:img1, link:'', desc:txt, },
    {id:2, name:'نام پروژه',img:img2, link:'', desc:txt, },
    {id:3, name:'نام پروژه',img:img3, link:'', desc:txt, },
]

const animVariant = {
    initialLeft:{x:-100, opacity:0},
    initialRight:{x:+100, opacity:0},
    start: {
        x:0, 
        opacity:1,
        transition: { duration: .6, delay: .2}
    }
}

const ShowCards = () => {
    return(
        list.map((item, key)=>(
            window.innerWidth >= 1024 ?
                key % 2 === 0 ?
                    <motion.div 
                        key={key}
                        className='PCBox'
                        variants={animVariant}
                        initial='initialRight'
                        whileInView='start'
                        viewport={{ once: true }}
                    >
                        <div className='PCBoxImg' >
                            <motion.img  
                                src={item.img} 
                                alt={item.name} 
                                whileHover={{scale:1.08}}
                            />
                        </div>
                        <div className='PCBoxBody'>
                            <h3 className='h3'>{item.name}</h3>
                            <p className='txtBody'>{item.desc}</p>
                        </div>
                    </motion.div>
                :
                    <motion.div 
                        key={key}
                        className='PCBox'
                        variants={animVariant}
                        initial='initialLeft'
                        whileInView='start'
                        viewport={{ once: true }}
                    >
                        <div className='PCBoxBody'>
                            <h3 className='h3'>{item.name}</h3>
                            <p className='txtBody'>{item.desc}</p>
                        </div>
                        <div className='PCBoxImg' >
                            <motion.img  
                                src={item.img} 
                                alt={item.name} 
                                whileHover={{scale:1.08}}
                            />
                        </div>
                    </motion.div>
                :
                    <motion.div 
                        key={key}
                        className='PCBox'
                        variants={animVariant}
                        initial='initialRight'
                        whileInView='start'
                        viewport={{ once: true }}
                    >
                        <div className='PCBoxImg' >
                            <motion.img  
                                src={item.img} 
                                alt={item.name} 
                                whileHover={{scale:1.08}}
                            />
                        </div>
                        <div className='PCBoxBody'>
                            <h3 className='h3'>{item.name}</h3>
                            <p className='txtBody'>{item.desc}</p>
                        </div>
                    </motion.div>
        ))
    )
}

const ProjCard = () => {
  return (
    <section className='gap_2 boxColumn'>
      <ShowCards />
      <Link to="/AcceptProject" className='btn_transparent' style={{marginTop:'-4rem'}} >درخواست سفارش پروژه</Link>
    </section>
  )
}

export default ProjCard
